* {
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

body {
  height: 100vh;
  background-color: #f2f2f2;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.container {
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border-radius: 5px;
  padding: 40px;
  box-shadow: 0 2px 5px #0000004d;
}

h2 {
  text-align: center;
  margin-bottom: 40px;
}

form {
  flex-direction: column;
  display: flex;
}

label {
  margin-bottom: 5px;
  font-weight: bold;
}

input {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 14px;
}

button.login-btn {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

button.login-btn:hover {
  background-color: #45a049;
}

a {
  color: #4caf50;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

